<template>
  <div class="header">
    <div class="content-wrapper">
      <div class="avatar">
        <img v-bind:src="seller.avatar" width="64" height="64">
      </div>
      <div class="content">
        <div class="title">
          <span class="brand"></span>
          <span class="name">{{seller.name}}</span>
        </div>
        <div class="description">
          {{seller.description}}/{{seller.deliveryTime}}分钟送达
        </div>
        <div v-if="seller.supports" class="supports">
        <span class="icon" v-bind:class="classMap[seller.supports[0].type]">

        </span>
          <span class="text">
          {{seller.supports[0].description}}
        </span>
        </div>
      </div>
      <div class="support-count" v-if="seller.supports" @click="showDetail()">
        <span class="count">{{seller.supports.length}}个</span>
        <i class="icon-keyboard_arrow_right"></i>
      </div>

    </div>
    <div class="bulletin-wrapper" @click="showDetail()">
      <span class="bulletin-title"></span><span class="bulletin-text">{{seller.bulletin}}</span>
      <i class="icon-keyboard_arrow_right"></i>
    </div>
    <div class="background">
      <img v-bind:src="seller.avatar"  width="100%" height="100%">
    </div>
    <transition name="fade">
        <div class="detail" v-show="detailShow" >
          <div class="detail-wrapper clearfix" >
          <div class="detail-main">
            <h1 class="name">{{seller.name}}</h1>
            <star :size="48" :score="seller.score"></star>
            <div class="title">
              <div class="line"></div>
              <div class="text">优惠信息</div>
              <div class="line"></div>
            </div>
            <ul v-if="seller.supports" class="supports">
              <li class="support-item" v-for="(item, index) in seller.supports">
                <span class="icon" v-bind:class="classMap[seller.supports[index].type]"></span>
                <span class="text">{{seller.supports[index].description}}</span>
              </li>
            </ul>
            <div class="title">
              <div class="line"></div>
              <div class="text">商家公告</div>
              <div class="line"></div>
            </div>
            <div class="bulletin">
              <p class="content">
                {{seller.bulletin}}
              </p>
            </div>
          </div>

        </div>
      <div class="detail-close">
        <i class="icon-close" @click="hideDetail"></i>
      </div>

    </div>
    </transition>

  </div>
</template>

<script>
  import  star from '../../components/star/star.vue'
  export default {
      props:{
          seller:{
              type:Object
          }
      },
      data(){
        return {
            detailShow:false
        }
      },
      methods:{
          showDetail(){
            this.detailShow=true;
          },
          hideDetail(){
            this.detailShow=false;
          }
      },
      created(){
        this.classMap=[
            'decrease','discount',"special","invoice","guarantee"
        ]
      },
      components:{
          star
      }
  }
</script>

<style lang="less" rel="stylesheet/less">
  @import '../../common/less/index.less';

  .header{
    color:#fff;
    position: relative;
    overflow: hidden;
    background:rgba(7,17,27,0.5);
    .content-wrapper{
      padding:24px 12px 18px 24px;
      font-size:0;
      position:relative;
      .avatar{
        display: inline-block;
        vertical-align: top;
        img{
          border-radius: 2px;
        }
      }
      .content{
        display: inline-block;
        font-size: 14px;
        margin-left:16px;
        .title{
          margin:2px 0 8px 0;
          .brand{
            display: inline-block;
            vertical-align: top;
            width:30px;
            height: 18px;
            background-size:30px 18px;
            background-repeat: no-repeat ;
            .bg(brand);
          }
          .name{
            margin-left:6px;
            font-size: 16px;
            line-height: 18px;
            font-weight:bold;
          }
        }
        .description{
          margin-bottom:10px;
          line-height: 12px;
          font-size: 12px;
        }
        .supports{
          font-size:0;
          .icon{
            display: inline-block;
            vertical-align: bottom;
            width:12px;
            height:12px;
            margin-right:4px;
            background-size:12px 12px;
            background-repeat: no-repeat ;
            &.decrease{
              .bg(decrease_1);
             }
            &.discount{
             .bg(discount_1);
             }
            &.guarantee{
              .bg(guarantee_1)
            }
            &.invoice{
           .bg(invoice_1)
           }
            &.special{
             .bg(special_1)
             }
          }
          .text{
            font-size:12px;
            line-height: 12px;
          }
        }
      }
      .support-count{
        position:absolute;
        right:12px;
        bottom: 14px;
        padding:0 8px;
        height:24px;
        line-height: 24px;
        border-radius: 14px;
        background-color:rgba(0,0,0,0.2) ;
        text-align: center;
        .count{
          font-size:10px;
          vertical-align: top;
        }
        .icon-keyboard_arrow_right{
         line-height: 24px;
          margin-left:2px;

          font-size:10px;
        }
      }
    }
    .bulletin-wrapper{
      position: relative;
      height:28px;
      line-height: 28px;
      padding:0 22px 0 12px;
      white-space:nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      background:rgba(7,17,27,0.2);
      .bulletin-title{
        display: inline-block;
        vertical-align: top;
        width:22px;
        height:12px;
        .bg(bulletin);
        background-repeat: no-repeat;
        background-size:22px 12px;
        margin-top:8px;
      }
      .bulletin-text{
        vertical-align: top;
        font-size: 10px;
        margin:0 4px;
      }
      .icon-keyboard_arrow_right{
        position: absolute;
        font-size: 10px;
        right:12px;
        top:8px;
      }
    }
    .background{
      position: absolute;
      top:0;
      left:0;
      width:100%;
      height:100%;
      z-index:-1;
      filter:blur(10px);
    }
    .detail{
      position: fixed;
      z-index:100;
      width:100%;
      height:100%;
      overflow: auto;
      transition:all 0.5s;
      top:0;
      left:0;
      background: rgba(7,17,27,0.8);
      &.fade-enter-active, .fade-leave-active {
        transition: all .5s
      }
      &.fade-enter, .fade-leave-active {
        opacity: 0
      }
      .detail-wrapper{
        width:100%;
        min-height: 100%;
        .detail-main{
          margin-top:64px;
          padding-bottom:64px;
          .name{
            line-height: 16px;
            text-align: center;
            font-size: 16px;
            font-weight:700;
          }
          .star{
            margin-top:18px;
            text-align: center;
            padding:2px 0;
          }
          .title{
            display: flex;
            width:80%;
            margin:30px auto 24px auto;
            .line{
              flex:1;
              position:relative;
              top:-6px;
              border-bottom: 1px solid rgba(255,255,255,0.2);
            }
            .text{
              padding:0 12px;
              font-size: 14px;
              font-weight:700;
            }
          }
          .supports{
            width:80%;
            margin:0 auto;
            .support-item{
              padding:0 12px;
              margin-bottom: 12px;
              font-size: 0;
              &:last-child{
                margin-bottom: 0;
              }
              .icon{
                display: inline-block;
                width:16px;
                height:16px;
                vertical-align: top;
                margin-right:6px;
                background-size:16px 16px;
                background-repeat: no-repeat;
                &.decrease{
                  .bg(decrease_2);
                }
                &.discount{
                  .bg(discount_2);
                }
                &.guarantee{
                  .bg(guarantee_2)
                }
                &.invoice{
                  .bg(invoice_2)
                }
                &.special{
                  .bg(special_2)
                }
              }
              .text{
                line-height: 16px;
                font-size: 12px;
              }
            }
          }
          .bulletin{
            width:80%;
            margin:24px auto;
            .content{
              padding:0 12px;
              font-size: 12px;
              line-height: 24px;
            }
          }
        }
      }
      .detail-close{
        position: relative;
        width:32px;
        height:32px;
        margin:-64px auto 0 auto;
        clear:both;
        font-size: 32px;
      }
    }
  }
</style>



